import React, { useEffect, useState } from 'react';
import { useLocation } from '@reach/router';
import { fetchPriceItemById, savePriceItem } from '../../tables/tables';
import Layout from '../../components/Layout';

export default function ItemEditor() {
  const location = useLocation();
  const pathParts = location.pathname.split('/').filter(Boolean);
  const rawId = pathParts[1] || '';
  const id = decodeURIComponent(rawId);

  const [item, setItem] = useState<any>(null);
  const [saving, setSaving] = useState(false);
  const [success, setSuccess] = useState('');
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const load = async () => {
      const data = await fetchPriceItemById(id);
      if (!data) {
        setError('‚ùå Item not found.');
        setLoading(false);
        return;
      }

      const costEa = data.bulk / data.carton;
      const profit = data.price_each - costEa;

      setItem({
        ...data,
        costEa,
        profit,
      });

      setLoading(false);
    };

    load();
  }, [id]);

  const updateField = (field: 'bulk' | 'carton' | 'price_each', value: number) => {
    const updated = { ...item, [field]: value };

    const costEa = updated.bulk / updated.carton;
    const profit = updated.price_each - costEa;

    updated.costEa = costEa;
    updated.profit = profit;

    setItem(updated);
  };

  const handleSave = async () => {
    setSaving(true);
    setError('');
    setSuccess('');

    const payload = {
      bulk: parseFloat(item.bulk),
      carton: parseInt(item.carton),
      price_each: parseFloat(item.price_each),
    };

    const res = await savePriceItem(item.uid, payload);

    if (res?.error) {
      setError('‚ùå Failed to save.');
    } else {
      setSuccess('‚úÖ Item updated!');
    }

    setSaving(false);
  };

  const currentMargin =
    item && item.price_each > 0 ? (item.profit / item.price_each) * 100 : 0;
 


  return (
     <Layout>
      { loading ? <div style={{ padding: 20 }}>Loading...</div> : 
    <div style={{ padding: 20, fontFamily: 'Arial' }}>
      <h2>Edit Item: {item.name}</h2>

      <table border={1} cellPadding={6} cellSpacing={0}>
        <thead>
          <tr>
            <th>Item</th>
            <th>Bulk</th>
            <th>Carton</th>
            <th>Cost Ea.</th>
            <th>Sell</th>
            <th>Profit</th>
            <th>Margin</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{item.name}</td>
            <td>
              <input
                type="number"
                value={item.bulk}
                onChange={(e) => updateField('bulk', parseFloat(e.target.value) || 0)}
                style={{ width: 80 }}
              />
            </td>
            <td>
              <input
                type="number"
                value={item.carton}
                onChange={(e) => updateField('carton', parseInt(e.target.value) || 1)}
                style={{ width: 60 }}
              />
            </td>
            <td>${item.costEa.toFixed(2)}</td>
            <td>
              <input
                type="number"
                value={item.price_each}
                onChange={(e) => updateField('price_each', parseFloat(e.target.value) || 0)}
                style={{ width: 80 }}
              />
            </td>
            <td>${item.profit.toFixed(2)}</td>
            <td>{currentMargin.toFixed(1)}%</td>
          </tr>
        </tbody>
      </table>

      <button onClick={handleSave} disabled={saving} style={{ marginTop: 20, padding: '8px 16px' }}>
        {saving ? 'Saving...' : 'üíæ Save'}
      </button>

      {error && <p style={{ color: 'red' }}>{error}</p>}
      {success && <p style={{ color: 'green' }}>{success}</p>}
    </div>}
    </Layout>
  );
}
